.whyus > h1 {
  margin: 2rem;
  text-align: center;
  color: #ffffff;
  font-size: 5rem;
}

.whyus .whyus-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.whyus .whyus-box .message-box {
  width: 40rem;
  height: 200px;
  font-size: 2rem;
  color: #0b2447;
  background-color: aliceblue;
  padding: 1rem 2rem;
  margin: 2rem;
  border-radius: 15px;
}

@media (max-width: 1150px) {
  .App .main-section .left {
    z-index: 0;
    position: relative;
    left: 0;
    color: bisque;
  }
  .App .main-section .center {
    z-index: 1;
    margin: 0 -27rem;
    color: #0b2447;
  }
}

@media (max-width: 1000px) {
  .whyus .whyus-box .message-box {
    width: 30rem;
    height: 230px;
  }
}
